'use client'
import { useAuth } from "@/components/AuthContext" 
import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { DeleteIcon, Edit, MoreHorizontal } from "lucide-react"
import { bookService } from "@/lib/services/bookService"
import { Skeleton } from "@/components/ui/skeleton"
import { DropdownMenu } from "@radix-ui/react-dropdown-menu"
import { DropdownMenuContent, DropdownMenuLabel, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"
import { Button } from "@/components/ui/button"


import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import AddBook from "@/components/book/AddBook"
import { Separator } from "@/components/ui/separator"
import { BookCreateModel } from "@/type/book"

import {
  Pagination,
  PaginationContent,
  PaginationEllipsis,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination"
import { usePagination } from "@/hook/usePagination"

export default function Page(){
    const {isAuthenticated} = useAuth()
    const router = useRouter()
    const [booksData, setBooksData] =useState([])
    const [isLoading,setIsLoading] = useState(false)
    const [open, setOpen] = useState(false)
    const [actionType,setActionType] = useState("add")
    const [bookDetail, setBookDetail] = useState<BookCreateModel>()
    const pagination = usePagination()
    

    

    useEffect(()=>{
       if(!isAuthenticated){
            router.push('/login')
        }
    },[isAuthenticated])

    useEffect(()=>{
      
      getBooks()
    },[pagination.page,pagination.search])

    

    const getBooks = ()=>{

        bookService.getBooks({page:pagination.page,search:pagination.search,page_size:pagination.pageSize}).then((res)=>{
          setIsLoading(false)
          setBooksData(res.data.data.results)
          pagination.setTotalCount(res.data.data.count)
        }).catch(error=>{
          setIsLoading(false)
          console.log(error)
        }) 
    }

    const handleUpdateButton=(bookDetail:BookCreateModel)=>{
          setBookDetail(bookDetail)
        
          setActionType("edit")
            setOpen(true)
    }

    const handleDeleteBook = async(id:string)=>{
      setIsLoading(true)
      bookService.deleteBook(id).then((res)=>{
        setIsLoading(false)
        setBooksData((prev)=>{
          return prev.filter((book:any)=>book?.id !== id)
        })
      }).catch(error=>{
        setIsLoading(false)
        console.log(error)
      })
    }

    useEffect(()=>{
         if(!open){
          setBookDetail({
            id: "",
            title: "",
            author: "",
            published_date: "",
            language: "",
            type: "",
            quantity: 0
          })
          setActionType('add')
         }
    },[open])

  
   


    
    return(
        <div className="w-full">
            <div className="flex justify-between items-center px-12 py-4">
              <input className="max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" 
              placeholder="search"
              onChange={(e)=>pagination.setSearch(e.target.value)}
              />
              
              <Dialog open={open} onOpenChange={setOpen}>
  <DialogTrigger className="bg-black hover:bg-gray-500 cursor-pointer text-white px-4 py-2 rounded-lg shadow-lg" >Add Book</DialogTrigger>
  <DialogContent>
    <DialogHeader>
      <DialogTitle>Add Book</DialogTitle>
    </DialogHeader>
    <Separator />
      <AddBook  setOpen={setOpen} getBooks={getBooks} actionType={actionType} bookDetail= {bookDetail}/>
    
  </DialogContent>
</Dialog>
            </div>
            <div className="flex flex-col justify-center">
<Table className="bg-white">
 
  <TableHeader>
    <TableRow>
      <TableHead >ID</TableHead>
      <TableHead>Title</TableHead>
      <TableHead>Language</TableHead>
      <TableHead>Quantity</TableHead>
      <TableHead>Type</TableHead>
      <TableHead>Auther</TableHead>
      <TableHead >Publish Date</TableHead>
      <TableHead >Owner</TableHead>
      <TableHead >Action</TableHead>
    </TableRow>
  </TableHeader>
  <TableBody>
    {!isLoading &&
       booksData.map((book:any,index)=>{
        return(
          <TableRow key={index}>
      <TableCell >{book?.id}</TableCell>
      <TableCell>{book?.title}</TableCell>
      <TableCell>{book?.language}</TableCell>
      <TableCell>{book?.quantity}</TableCell>
      <TableCell>{book?.type}</TableCell>
      <TableCell>{book?.author}</TableCell>
      <TableCell>{book.published_date}</TableCell>
      <TableCell className="">{book.owner}</TableCell>
      <TableCell className = "">
        
        <DropdownMenu>
          <DropdownMenuTrigger>
            <MoreHorizontal className="h-5 w-5 text-gray-400"/>
            </DropdownMenuTrigger>
          <DropdownMenuContent className="w-18">
             
             <DropdownMenuLabel className="flex items-center text-gray-700 cursor-pointer">
              <Popover >
                <PopoverTrigger className="flex items-center text-gray-700 cursor-pointer">
                   <DeleteIcon className="mr-4 text-[12px]"/>
              Delete
                </PopoverTrigger>
                <PopoverContent className="w-64">
                  are you sure to delete this book?
                  <div className="flex justify-center mt-4">
                      <Button className="text-red-600 bg-transparent outline-red-600">No</Button>
                      <Button className="text-green-600 bg-transparent outline-green-600" onClick={()=>handleDeleteBook(book.id)}>Yes</Button>
                  </div>
                  
                </PopoverContent>
              </Popover>
             </DropdownMenuLabel>
             <DropdownMenuLabel className="flex item-center text-gray-700 cursor-pointer" onClick={()=>handleUpdateButton(book)}>
              <Edit className="mr-4 text-[12px]"/>
              Update</DropdownMenuLabel>
          </DropdownMenuContent>
        </DropdownMenu>
        </TableCell>
    </TableRow>
        )
      })
    
    }
  </TableBody>
 
</Table>
 <Pagination className="self-start">
  <PaginationContent>
    <PaginationItem  onClick={isLoading ? undefined :pagination.handlePrevPage}>
      <PaginationPrevious  />
    </PaginationItem>
    {
      pagination.totalCount>0&&
      <>
      {
        <PaginationItem> 
           <PaginationLink>Total:{pagination.totalCount}</PaginationLink>
       </PaginationItem>
      }
     
   
    </>
    }
    
    <PaginationItem
    
      onClick={isLoading ? undefined : pagination.handleNextPage}
      className={booksData.length === 0 ? "pointer-events-none opacity-50 " : ""}
     
    >
      <PaginationNext />
    </PaginationItem>
  </PaginationContent>
</Pagination>
            </div>
            

        </div>
    )
}