
import axios from "../axios"



export const bookService ={
    getBooks,
    getBook,
    addBook,
    updateBook,
    deleteBook
}
function getBooks({page,search='',page_size}:{page:number,search:string,page_size:number}){
    return axios.get("/books/", {params:{page,search,page_size}})

}

function getBook(id:string){
    return axios.get("/books/"+id)
}

function addBook(book:any){
    return axios.post("/books/",book)
}

function updateBook(book:any){
    return axios.patch("/books/"+book.id +"/",book)
}

function deleteBook(id:string){
    return axios.delete("/books/"+id +"/")
}