'use client'
import {Form,FormControl,FormDescription,FormField, FormItem,FormLabel,FormMessage} from '@/components/ui/form'
import { useForm,  } from 'react-hook-form'
import { Input } from "@/components/ui/input"
import {Button} from "@/components/ui/button"
import { BookCreateModel } from '@/type/book'
import { Popover, PopoverContent, PopoverTrigger } from '../ui/popover'
import {  CalendarIcon, Check, ChevronsUpDown, Loader2 } from 'lucide-react'
import { Calendar } from "@/components/ui/calendar"
import { format } from "date-fns"
import { bookService } from '@/lib/services/bookService'
import { useMemo, useState } from 'react'

import { cn } from "@/lib/utils"
import toast from 'react-hot-toast'
import { Select, SelectContent, SelectItem, SelectLabel, SelectTrigger, SelectValue } from '../ui/select'
import { SelectGroup } from '@radix-ui/react-select'
import { usePagination } from '@/hook/usePagination'
import { Command, CommandEmpty, CommandGroup, CommandInput, CommandItem, CommandList } from '../ui/command'
import Link from 'next/link'



interface PropsType{
  setOpen:(open:boolean)=>void
  getBooks:()=>void
  actionType:string 
  bookDetail?:BookCreateModel
}
interface CategoryOption{
  name:string
  id:string
}
 
 export default function AddBook({setOpen,getBooks,actionType='add',bookDetail}:PropsType){
    const form = useForm<BookCreateModel>({ defaultValues: { ...bookDetail } });
     const [categories, setCategories] = useState<CategoryOption[]>([]);
    const [isLoading, setIsLoading] = useState(false)
    const pagination = usePagination()
    const [open,seOpenInline] = useState<boolean>(false)

    const onSubmit=(data:BookCreateModel)=>{

            // data.published_date = format(data.published_date)
            if(actionType==="add"){
            setIsLoading(true)
            bookService.addBook(data).then(()=>{
              toast.success("Book has been created sucessfuly")
              setIsLoading(false)
              setOpen(false)
              getBooks()
            }).catch((error)=>{
              setIsLoading(false)
              console.log(error)
            })
          } else if(actionType==="edit"){
             bookService.updateBook(data).then(()=>{
              toast.success("Book has been updated sucessfuly")
              setIsLoading(false)
              setOpen(false)
              getBooks()
             }).catch((error)=>{
                console.log(error)
             })
          }
    }

    const selectedCategoryName = useMemo(() => {
    return categories.find((category) => category.id === form.getValues("category"))?.name;
  }, [categories, form.getValues("category")]);


    return(
        <div className='mt-4'>
          <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)}>
          
            <FormItem className=''>
              <FormLabel>Title</FormLabel>
              <FormControl>
                <Input placeholder="title" {...form.register("title",{required:"title is required field"})} />
              </FormControl>
              {form.formState.errors.title&&
              <FormDescription className='text-red text-sm'>
                {form.formState.errors.title.message}
              </FormDescription>
              }
              <FormMessage />
            </FormItem>

            <FormItem className='mt-4'>
              <FormLabel>Author</FormLabel>
              <FormControl>
                  <Input placeholder='Author' {...form.register('author',{required:'author is required field'})}/>
              </FormControl>
              {
                form.formState.errors.author && 
                <FormDescription className='text-red text-sm'>
                  { form.formState.errors.author?.message}
                </FormDescription>
              }
            </FormItem>

            <FormItem className='mt-4'>
              <FormLabel>Language</FormLabel>
              <FormControl>
                <Input placeholder='Language' {...form.register('language',{required:'language is required field'})}/>
              </FormControl>
              {form.formState.errors.author &&
              <FormDescription className='text-red text-sm'>
                {
                  form.formState.errors.language?.message
                }
              </FormDescription>
              }
            </FormItem>

            <FormItem className='mt-4'>
              <FormLabel>
                Type
              </FormLabel>
              <FormControl>
                <Input placeholder='type' {...form.register('type', {required:'type is required field'})}/>
              </FormControl>
             {
              form.formState.errors.type && 
              <FormDescription className='text-red text-sm'>
                {form.formState.errors.type.message}
              </FormDescription>
             }
            </FormItem>

          

            <FormItem className='mt-4 mb-4'>
              <FormLabel>
                Quantity
              </FormLabel>
              <FormControl>
                <Input placeholder='quantity' {...form.register('quantity', {required:'quantity is required field'})}/>
              </FormControl>
              {form.formState.errors.type && 
               <FormDescription className='text-red text-sm'>
                {form.formState.errors.quantity?.message}
               </FormDescription>
              }
            </FormItem>

           <FormField
          control={form.control}
          name="category"
          render={({ field }) => (
            <FormItem className="flex flex-col">
              <FormLabel>Category</FormLabel>
              <Popover open={open} onOpenChange={(newOpen) => {
                setOpen(newOpen);
                if (!newOpen) {
                  pagination.setSearch(""); // Clear search term when closing
                } else {
                  // When opening, if there's no current search term,
                  // trigger an immediate fetch for initial categories.
                  // This is a common pattern for "on-demand" loading.
                  if (!pagination.search && categories.length === 0 && !isLoading) {
                     // If no search term and no categories loaded, and not already loading/error
                     // fetch immediately without debounce for initial open state
                     fetch("/api/categories")
                        .then(res => res.json())
                        .then(data => {
                           setCategories(data);
                           setLoading(false);
                        })
                        .catch(err => {
                           setError(err.message || "Failed to fetch categories.");
                           setLoading(false);
                        });
                  }
                }
              }}>
                <PopoverTrigger asChild>
                  <FormControl>
                    <Button
                      variant="outline"
                      role="combobox"
                      aria-expanded={open}
                      className={cn(
                        "w-[300px] justify-between",
                        !field.value && "text-muted-foreground"
                      )}
                      // Disabled state now depends only on whether the API call is in progress
                      disabled={loading && open} // Disable the button itself if actively loading and popover is open
                    >
                      {loading && open ? ( // Show loader in button ONLY if popover is open and loading
                        <span className="flex items-center">
                          <Loader2 className="mr-2 h-4 w-4 animate-spin" /> Loading...
                        </span>
                      ) : error && open ? ( // Show error in button ONLY if popover is open and error
                        <span className="text-red-500">Error loading categories</span>
                      ) : field.value ? (
                        selectedCategoryName || "Loading selected..." // Display name of selected, or "Loading" if not yet found
                      ) : (
                        "Select a category..."
                      )}
                      {!isLoading &&  <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />}
                    </Button>
                  </FormControl>
                </PopoverTrigger>
                <PopoverContent className="w-[300px] p-0">
                  <Command>
                    <CommandInput
                      placeholder="Search category..."
                      value={pagination.search} // Controlled input
                      onValueChange={pagination.setSearch} // Update search term state on change
                    />
                    <CommandList>
                      {!isLoading && open ? ( // Show loading indicator specifically when popover is open and fetching
                        <CommandEmpty className="py-6 text-center text-sm">
                          <Loader2 className="mr-2 h-4 w-4 animate-spin inline-block" /> Fetching categories...
                        </CommandEmpty>
                      )  : categories.length === 0 ? (
                        <CommandEmpty className="py-6 text-center text-sm">
                          No categories found.
                        </CommandEmpty>
                      ) : (
                        <CommandGroup>
                          {categories.map((category) => (
                            <CommandItem
                              value={category.name} // Search by name
                              key={category.id} // Use category.id as unique key
                              onSelect={() => {
                                form.setValue("category", category.id);
                                setOpen(false);
                                pagination.setSearch(""); // Clear search term after selection
                              }}
                            >
                              <Check
                                className={cn(
                                  "mr-2 h-4 w-4",
                                  category.id === field.value
                                    ? "opacity-100"
                                    : "opacity-0"
                                )}
                              />
                              {category.name}
                            </CommandItem>
                          ))}
                        </CommandGroup>
                      )}
                    </CommandList>
                  </Command>
                </PopoverContent>
              </Popover>
              <FormDescription>
                Select a product category for your item.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />
            
            <FormField
          
          control={form.control}
          name="published_date"
          render={({ field }) => (
            <FormItem className="flex flex-col mr-4">
              <FormLabel>Date</FormLabel>
              <Popover>
                <PopoverTrigger asChild>
                  <FormControl>
                    <Button
                      variant={"outline"}
                      className={cn(
                        "w-[240px] pl-3 text-left font-normal",
                        !field.value && "text-muted-foreground"
                      )}
                    >
                      {field.value ? (
                        format(field.value, "PPP")
                      ) : (
                        <span>Pick a date</span>
                      )}
                      <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                    </Button>
                  </FormControl>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={new Date(field.value)}
                    onSelect={field.onChange}
                    disabled={(date:any) =>
                      date > new Date() || date < new Date("1900-01-01")
                    }
                    captionLayout="dropdown"
                  />
                </PopoverContent>
              </Popover>
            
              <FormMessage />
            </FormItem>


          )}
        />
        <Button type="submit" className='mt-4 hover:bg-gray-600'>
          {isLoading?<span className="inline-block w-6 h-6 border-4 border-blue-500 border-t-transparent rounded-full animate-spin"></span>
          :
          actionType=="edit"?"Update Book":"Add Book"}</Button>
        </form>
         </Form>
        </div>
    )
}